import logo from './logo.svg';
import img_tx2_40 from './tx2-40.jpg';
import img_tx2_90 from './tx2-90.jpg';
import pdf_tx2_40 from './tx2-40.pdf';
import pdf_tx2_90 from './tx2-90.pdf';
import { List, Space } from 'antd';
import React from 'react';
import { MessageOutlined, LikeOutlined, StarOutlined } from '@ant-design/icons';
import { Document, Page } from 'react-pdf/dist/esm/entry.webpack'

import './App.css';

const data = [
  {
    title: 'TX2-40',
    image: img_tx2_40,
    specs: pdf_tx2_40
  },
  {
    title: 'TX2-90',
    image: img_tx2_90,
    specs: pdf_tx2_90
  }
];

const IconText = ({ icon, text }) => (
  <Space>
    {React.createElement(icon)}
    {text}
  </Space>
);

class App extends React.Component {

  constructor(props) {
    super(props);
    const id = 'antd-staubli-toggle';
    this.state = {
        loading: true,
        id: id,
        taskIdRequestData: `${id}-requestData`,
        data: [],
    };
    window.addEventListener("message", this.onMessageReceived, false);
    this.requestData();
  }

  onMessageReceived = event => 
  {
      if (event.data.taskId === this.state.taskIdRequestData) this.update(event);
  };

  requestData = () => 
  {
      window.parent.postMessage({
          iframe: this.state.id,
          taskId: this.state.taskIdRequestData,
          query: [{
              api: '/api/rd/v1/Configurator',
              function: 'getModel',
              arguments: [{
                'fakekey': 'fakevalue'
              }]
          }],
          response: [],
          status: 'request'
      }, "https://eusb.webcomcpq.com/");
  };

  update = event => 
  {
      // prepare data
      let model = event.data.response.find(r => r.api === '/api/rd/v1/Configurator' && r.function === 'getModel');

      // update state if data available
      this.setState({
          loading: false,
          data: model
      });
  };
  
  render() {
    return (
      <div className="App">
        <header className="App-header">
          <img src={logo} className="App-logo" alt="logo" style={{'height': '100px'}}/>
          <div className="App">
            { this.state.loading && 
              <span>loading data...</span>
            }
            { !this.state.loading && 
              <span>loaded!</span>
            }
            <List
              grid={{ gutter: 16, column: 2 }}
              dataSource={data}
              renderItem={item => (
                <List.Item
                style={{'background-color': 'white'}}
                actions={[
                  <IconText icon={StarOutlined} text="156" key="list-vertical-star-o" />,
                  <IconText icon={LikeOutlined} text="156" key="list-vertical-like-o" />,
                  <IconText icon={MessageOutlined} text="2" key="list-vertical-message" />,
                ]}
                extra={
                  <img width='200' height='200' style={{'mix-blend-mode': 'multiply'}} src={item.image} />
                }
                >
                  {/* <Card title={item.title} style={{'background-color': 'white'}}>
                    Card content
                  </Card> */}
                  <List.Item.Meta
                    title={<a>{item.title}</a>}
                  />
                </List.Item>
              )}
            />
          </div>
        </header>
      </div>
    );
  }
}

export default App;
